@font-face {
  font-family: "Source Sans Pro";
  font-style: normal;
  font-weight: 400;
  src: local(""),
    url("../fonts/source-sans-pro-v14-latin_cyrillic-regular.woff2") format("woff2"), /* Super Modern Browsers */
    url("../fonts/source-sans-pro-v14-latin_cyrillic-regular.woff") format("woff"), /* Modern Browsers */
    url("../fonts/source-sans-pro-v14-latin_cyrillic-regular.ttf") format("truetype"), /* Safari, Android, iOS */
}

@font-face {
  font-family: "Source Sans Pro";
  font-style: normal;
  font-weight: 600;
  src: local(""),
    url("../fonts/source-sans-pro-v14-latin_cyrillic-600.woff2") format("woff2"), /* Super Modern Browsers */
    url("../fonts/source-sans-pro-v14-latin_cyrillic-600.woff") format("woff"), /* Modern Browsers */
    url("../fonts/source-sans-pro-v14-latin_cyrillic-600.ttf") format("truetype"), /* Safari, Android, iOS */
}
