@font-face {
  font-family: "Source Sans Pro";
  font-style: normal;
  font-weight: 400;
  src: local(""), url("../fonts/source-sans-pro-v14-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/source-sans-pro-v14-latin_cyrillic-regular.woff") format("woff"), url("../fonts/source-sans-pro-v14-latin_cyrillic-regular.ttf") format("truetype"); }

@font-face {
  font-family: "Source Sans Pro";
  font-style: normal;
  font-weight: 600;
  src: local(""), url("../fonts/source-sans-pro-v14-latin_cyrillic-600.woff2") format("woff2"), url("../fonts/source-sans-pro-v14-latin_cyrillic-600.woff") format("woff"), url("../fonts/source-sans-pro-v14-latin_cyrillic-600.ttf") format("truetype"); }

.visually-hidden {
  position: absolute;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  white-space: nowrap; }

*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

.none {
  display: none; }

.wrapper {
  padding-left: 18px;
  padding-right: 18px; }
  @media (min-width: 450px) {
    .wrapper {
      padding-left: 25px;
      padding-right: 25px; } }
  @media (min-width: 900px) {
    .wrapper {
      padding-left: 35px;
      padding-right: 35px; } }

.list {
  margin: 0;
  padding: 0;
  list-style: none; }

.body {
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400; }

.body__nav {
  display: none; }
  @media (min-width: 900px) {
    .body__nav {
      display: block; } }
  @media (min-width: 1040px) {
    .body__nav {
      width: 25%; } }
  @media (min-width: 1400px) {
    .body__nav {
      width: 20%; } }

.body__main {
  margin-bottom: 40px; }
  @media (min-width: 1040px) {
    .body__main {
      padding-left: 40px;
      padding-right: 40px;
      display: flex; } }

.promo {
  min-height: 248px;
  padding: 110px 0 18px;
  background-image: url("../img/bg_promo.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center; }
  @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
    .promo {
      background-image: url("../img/bg_promo@2x.jpg");
      background-size: auto; } }
  @media (min-width: 450px) {
    .promo {
      min-height: 300px;
      padding-bottom: 30px; } }
  @media (min-width: 768px) {
    .promo {
      min-height: 350px;
      padding-bottom: 50px; } }
  @media (min-width: 1040px) {
    .promo {
      min-height: 242px;
      padding: 25px;
      background-image: url("../img/bg_promo-desktop.jpg"); } }
  @media (min-width: 1040px) and (-webkit-min-device-pixel-ratio: 2), (min-width: 1040px) and (min-resolution: 2dppx) {
    .promo {
      background-image: url("../img/bg_promo-desktop@2x.jpg");
      background-size: auto; } }

.promo__wrapper {
  margin: auto;
  width: 90%;
  padding: 35px 20px 25px;
  background-color: rgba(255, 255, 255, 0.7); }
  @media (min-width: 450px) {
    .promo__wrapper {
      width: 80%; } }
  @media (min-width: 768px) {
    .promo__wrapper {
      width: 70%; } }
  @media (min-width: 1040px) {
    .promo__wrapper {
      margin: 0;
      width: 36%; } }
  @media (min-width: 1040px) {
    .promo__wrapper {
      padding: 60px 20px 40px; } }

.promo__title {
  margin: 0;
  padding: 0;
  font-size: 24px;
  font-weight: 600;
  letter-spacing: -0.5px;
  text-align: center; }
  @media (min-width: 450px) {
    .promo__title {
      font-size: 26px;
      line-height: 30px; } }
  @media (min-width: 768px) {
    .promo__title {
      font-size: 30px;
      line-height: 34px; } }
  @media (min-width: 1040px) {
    .promo__title {
      font-weight: 600;
      font-size: 40px;
      line-height: 38px; } }

.promo__link-all {
  display: block;
  width: 55%;
  max-width: 190px;
  margin: auto;
  margin-top: 15px;
  padding: 6px 10px;
  font-size: 9px;
  font-weight: 600;
  line-height: 12px;
  text-decoration: none;
  color: inherit;
  border: 1px solid #000000;
  text-align: center; }
  @media (min-width: 450px) {
    .promo__link-all {
      max-width: 220px;
      margin-top: 25px;
      padding: 10px 10px;
      font-size: 12px; } }
  @media (min-width: 768px) {
    .promo__link-all {
      margin-top: 35px;
      padding: 15px 15px;
      font-size: 16px;
      line-height: 18px; } }
  @media (min-width: 1040px) {
    .promo__link-all {
      width: 60%;
      max-width: 210px;
      margin-top: 10px;
      padding: 12px 15px;
      font-size: 14px;
      line-height: 18px;
      font-weight: 400; } }

.nav {
  position: relative; }
  @media (min-width: 900px) {
    .nav {
      padding-left: 40px;
      padding-right: 40px;
      margin-top: 35px; } }
  @media (min-width: 1040px) {
    .nav {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 0; } }
  .nav a {
    color: inherit;
    text-decoration: none; }

.nav__title {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  font-size: 24px;
  line-height: 28px; }
  @media (min-width: 1040px) {
    .nav__title {
      font-size: 20px;
      line-height: 24px;
      font-weight: 400; } }

.nav__subtitle {
  margin: 0;
  padding: 0;
  margin-top: 20px;
  font-size: 24px;
  line-height: 24px; }
  @media (min-width: 1040px) {
    .nav__subtitle {
      margin-top: 20px;
      font-size: 14px;
      line-height: 20px;
      font-weight: 400; } }

.nav__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 25px;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400; }
  @media (min-width: 1040px) {
    .nav__list {
      margin-top: 15px; } }
  @media (min-width: 1040px) {
    .nav__list {
      flex-grow: 2;
      justify-content: space-between;
      margin: 0;
      font-size: 12px;
      line-height: 14px; } }

.nav__item {
  width: 25%; }
  @media (min-width: 1040px) {
    .nav__item {
      width: 100%; } }

.nav__link {
  display: block;
  padding: 10px 15px; }
  @media (min-width: 1040px) {
    .nav__link {
      padding: 5px 10px; } }
  @media (min-width: 1040px) {
    .nav__link {
      padding: 2px 10px; } }

.goods {
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 15px; }
  @media (min-width: 450px) {
    .goods {
      margin-top: 35px; } }
  @media (min-width: 768px) {
    .goods {
      margin-top: 55px; } }
  @media (min-width: 1040px) {
    .goods {
      padding-left: 0;
      padding-right: 0;
      width: 75%;
      margin-top: 35px; } }
  @media (min-width: 1040px) {
    .goods {
      width: 80%; } }

.goods__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around; }
  @media (min-width: 1040px) {
    .goods__list {
      padding-left: 30px;
      padding-right: 30px;
      display: grid;
      gap: 20px;
      grid-template-columns: 25% 25% 25% 25%;
      grid-auto-rows: 1fr; } }
  @media (min-width: 1400px) {
    .goods__list {
      padding-left: 40px;
      padding-right: 40px;
      grid-template-columns: 20% 20% 20% 20% 20%; } }

.goods__promo-photo {
  display: none; }
  @media (min-width: 1040px) {
    .goods__promo-photo {
      display: block;
      grid-column: 1/3;
      grid-row: 1/3; }
      .goods__promo-photo img {
        width: 100%;
        height: 100%; } }
  @media (min-width: 1400px) {
    .goods__promo-photo {
      width: 100%; } }

.goods__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 45%;
  margin-bottom: 35px; }
  @media (min-width: 450px) {
    .goods__item {
      width: 40%;
      margin-bottom: 45px; } }
  @media (min-width: 768px) {
    .goods__item {
      width: 30%; } }
  @media (min-width: 1040px) {
    .goods__item {
      width: 100%;
      margin: 0; } }
  @media (min-width: 1400px) {
    .goods__item {
      margin: 0; } }
  .goods__item img {
    width: 100%; }

.goods__link {
  display: block;
  width: 100%;
  margin: auto;
  margin-bottom: 0;
  padding: 6px 10px;
  font-size: 9px;
  font-weight: 600;
  line-height: 12px;
  text-decoration: none;
  color: inherit;
  border: 1px solid #000000;
  text-align: center; }
  @media (min-width: 450px) {
    .goods__link {
      padding: 10px 10px;
      font-size: 12px; } }
  @media (min-width: 768px) {
    .goods__link {
      padding: 15px 15px;
      font-size: 16px;
      line-height: 18px; } }
  @media (min-width: 1040px) {
    .goods__link {
      font-weight: 400; } }
